.media_Header::after{
content: "";
position: absolute;
width: 100%;
height: 2px;
background: linear-gradient(to right, red , yellow);
top: 100%;
}
.media_Header::before{
    content: "";
    position: absolute;
    width: 25%;
    height: 2px;
    background: linear-gradient(to right, red , yellow);
    top: 0%;
    left:0%
    }
    